@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Service {
    overflow-y: hidden;
    position: relative;
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    background-color: #eff1f2;
}

.Service h1 {
    position: absolute;
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 900;
    color: #2789F6;
    top: 100px;
    letter-spacing: 15px;
}

.Service::before {
    content: "";
    position: absolute;
    border: 1.5px solid #2789F6;
    background-color: #2789F6;;
    height: 70px;
}

.Service-body {
    width: 100%;
    height: 750px;
}

.Service-part {
    width: 100%;
    height: 700px;
    position: relative;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
}

.icon {
    text-decoration: none;
    color: #2789F6;
}

.icon h3,
.icon p {
    margin-top: 20px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.slider {
    position: relative;
    width: 100%;
    height: 400px;
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #2789F6;
    outline: none;
    z-index: -1;
}

.slider h2 {
    margin-bottom: 30px;
    font-size: 50px;
}

.slider p:nth-child(2) {
    width: 400px;
    margin-bottom: 60px;
    font-size: 18px;
    text-align: justify;
}

.slider p:nth-child(3) {
    text-align: center;
}
.About {
    overflow-y: hidden;
    position: relative;
    width: 100%;
    height: 900px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -5px;
}

.About::before {
    content: "";
    position: absolute;
    border: 1.5px solid #2789F6;
    background-color: #2789F6;
    height: 70px;
    top: -10px;
}


.About h1 {
    position: absolute;
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 900;
    color: #2789F6;
    top: 80px;
    letter-spacing: 15px;
}

.About-content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 50px;
    width: 100%;
    height: 100%;
}

.About-content img {
    width: 450px;
    height: 500px;
}

.About-content .before::before {
    content: "";
    position: absolute;
    width: 450px;
    height: 500px;
    border: 3px solid  #2789F6;
    transform: translate(-15%, -13%);
    z-index: -1;
}

.About-text {
    padding-left: 50px;
    width: 30%;
    height: 50%;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #2789F6;
    margin-left: 11%;
}

.About-text p {
    font-size: 20px;
}

.About-text button {
    width: 180px;
    height: 50px;
    border: none;
    background-color: #2789F6;
    border-radius: 25px;
    margin-top: 100px;
    color: white;
    box-shadow: 4px 4px 2px rgba(0,0,0,0.2);
}

.footer-contents {
    position: relative;
    width: 100%;
    height: 200px;
    background-color:  #2789F6;
    color: #fff;
}

.footer-contents footer {
    max-width: 100%;
    /* position: absolute; */
    height: 200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.footer-contents .footer-contents_f,
.footer-contents .footer-contents_s,
.footer-contents .footer-contents_t {
    font-size: 16px;
    /* margin: 0 50px; */
    padding-bottom: 60px;
    text-align: center;
}

.footer-contents .footer-contents_f p,
.footer-contents .footer-contents_s p,
.footer-contents .footer-contents_t p{
    padding-top: 10px;
}




.footer-contents .footer-contents_f::after {
    content: "";
    position: absolute;
    border: 1px solid #fff;
    height: 60px;
    left: 25%;
    top: 25%;
}

.footer-contents .footer-contents_s::after {
    content: "";
    position: absolute;
    border: 1px solid #fff;
    height: 60px;
    left: 50%;
    top: 25%;
}

.footer-contents .footer-contents_t::after {
    content: "";
    position: absolute;
    border: 1px solid #fff;
    height: 60px;
    left: 75%;
    top: 25%;
}

.footer-contents .footer-contents_f:nth-child(1) {
    font-family: 'Oswald', sans-serif;
    font-weight: 600;
}

.footer-contents .footer-contents_ss {
    position: absolute;
    bottom: 0;
    margin-bottom: 20px;
}

.footer-contents .footer-contents_sf {
    color: #fff;
    font-size: 20px;
    top: -10%;
    position: relative;
}

.footer-contents .footer-contents_sf ul{
    display: flex;
}

.footer-contents .footer-contents_sf ul li {
    padding: 0 10px;
}
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    /* overflow-y: auto; */
}



.bgWith {
    position: absolute;
    -webkit-animation: leftToright 1s 1s forwards;
            animation: leftToright 1s 1s forwards;
    top: 81px;
    min-width: 100%;
    min-height: 1080px;
}



@-webkit-keyframes leftToright {
    from {
        transform: translateX(-100%);
        /* opacity: 0; */
    }

    to {
        width: 50%;
        height: 540px;
        background-color: rgba(39, 137, 246, 0.7);
        transform: translateX(-50%);
        transition: all 0.1s ease-in-out;
    }
}



@keyframes leftToright {
    from {
        transform: translateX(-100%);
        /* opacity: 0; */
    }

    to {
        width: 50%;
        height: 540px;
        background-color: rgba(39, 137, 246, 0.7);
        transform: translateX(-50%);
        transition: all 0.1s ease-in-out;
    }
}

.main-font {
    width: 100%;
    height: 100%;
    position: absolute;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
    color: rgba(0, 0, 0, 0.5);
    bottom: 30px;
    left: 15px;

}

.main-font h1 {
    -webkit-animation: fontControl 2s ease-in-out;
            animation: fontControl 2s ease-in-out;
    padding: 10px 0;
}

@-webkit-keyframes fontControl {
    from {
        opacity: 0;
        transform: translateY(-30%);
    }

    to {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes fontControl {
    from {
        opacity: 0;
        transform: translateY(-30%);
    }

    to {
        opacity: 1;
        transform: translateY(0%);
    }
}

.main-font h1:nth-child(1) {
    font: normal normal normal 50px sans-serif;
}

.main-font h1:nth-child(2),
.main-font h1:nth-child(3) {
    font-weight: 900;
    font-size: 80px;
    letter-spacing: 10px;
    text-transform: uppercase;
}

.main-font h1:nth-child(3) {
    position: relative;
    bottom: 40px;
}

/* End Navbar */
nav {
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 10px 0; */
}

nav .navbar-left {
    position: absolute;
    left: 50px;
}

nav .navbar-right {
    width: 100%;
}

.navbar ul {
    position: relative;
    display: flex;
    justify-content: center;
    /* left: 20%; */
    z-index: 2;
}

.navbar ul li {
    list-style-type: none;
    padding: 30px 0;
}

.navbar ul li a {
    padding: 30px 70px;
    text-decoration: none;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 400;
    color: #000;
}

.navbar ul li a:hover {
    color: #2789F6;
    transition: all 0.3s;
}
.join_complete {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.join_complete .join_complete_contents {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 100px 150px;
    border: 1px solid #ddd;
}

.join_complete strong {
    display: block;
    font-size: 1.875rem;
    font-weight: 500;
    color: #333;
}

.join_complete p {
    display: block;
    line-height: 2.2;
    font-size: 1.125rem;
    color: #333;
}

.join_complete img {
    display: block;
    margin: 30px auto 50px;
}
.join_form .join_tit {
    position: relative;
}

.join_form .join_tit h4 {
    padding: 0 0 10px 0;
    font-size: 1.375rem;
    font-weight: 500;
    color: #333;
    border-bottom: 2px solid #333;
    text-align: left;
}

.join_form .join_tit .chk_box {
    position: absolute;
    top: 10px;
    right: 0;
    padding: 0;
}

.join_form .join_tit .chk_box input[type="checkbox"]+label p {
    font-size: 1rem;
}

.join_form .join_tit .chk_box input[type="checkbox"]+label span {
    margin: 0 0 0 10px;
}

.join_form>strong {
    display: block;
    padding: 25px 0 10px;
    font-size: 1rem;
    font-weight: 500;
    color: #333;
}

.join_form .data_box {
    box-sizing: border-box;
    overflow-y: scroll;
    width: 100%;
    height: 200px;
    padding: 20px;
    background-color: #f6f6f6;
    border: 1px solid #ddd;
}

.join_form .chk_box {
    padding: 15px 0 25px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
}

.join_form .chk_box input {
    position: relative;
    top: -6px;
    margin-right: 10px;
}
.steper {
    position: relative;
    width: 100%;
    height: 936px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.write_form {
    position: relative;
    width: 1281px;
    height: 590px;
}

.write_form .row {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #ddd;

}

.write_form .row .label {
    float: left;
    box-sizing: border-box;
    width: 150px;
    height: 60px;
    padding: 18px 0 0 20px;
    font-size: 1rem;
    font-weight: 500;
    color: #333;
    background-color: #f6f6f6;
    text-align: left;
}

.write_form .row .input_box {
    position: relative;
    box-sizing: border-box;
    height: 100%;
    padding: 10px;
    font-size: 1rem;
    font-weight: 400;
    color: #333;
}

input {
    box-sizing: border-box;
    font-size: 16px;
    border: 1px solid #ddd;
    background-color: #fff;
}

button {
    box-sizing: border-box;
    font-size: 1rem;
    border: 0;
    cursor: pointer;
    background-color: #bbb;
}

.write_form .row .input_box input {
    width: 250px;
    height: 40px;
    padding: 0 20px;
    vertical-align: top;
}

.write_form .row .id input {
    float: left;
    margin: 0 10px 0 0;
}

.write_form .row .input_box button {
    height: 40px;
    padding: 0 20px;
    vertical-align: top;
    font-size: 1rem;
    font-weight: 500;
    color: #fff;
}



.write_form .row.post_address .label {
    height: 170px;
}

.write_form .row.post_address .input_box div input {
    /* float: left; */
    width: 330px;
}

.write_form .row.post_address .input_box > input {
    width: 100%;
    margin: 10px 0 0 0;
}




.write_form .row .input_box.phone,
.write_form .row .input_box.email {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 50%;
}

.write_form .row .input_box.phone .marketing,
.write_form .row .input_box.email .marketing {
    position: relative;
    display: flex;
    flex-direction: row;
    /* margin-left: 40px; */
    left: 40px;
    align-items: center;
}

.write_form .row .input_box.phone .marketing .chk_box,
.write_form .row .input_box.email .marketing .chk_box {
    position: relative;
    display: flex;
    flex-direction: row;
}

.write_form .row .input_box.phone .marketing .chk_box .chk_box_input,
.write_form .row .input_box.email .marketing .chk_box .chk_box_input {
    width: 50px;
    height: 20px;
}
.inner {
    position: relative;
    box-sizing: border-box;
    width: 1200px;
    height: 800px;
    margin: 0 auto;
    top: 100px;
}

.sub_tit {
    display: block;
    padding: 70px 0 50px;
    font-size: 2.5rem;
    color: #333;
    text-align: center;
}

.sub_txt {
    font-size: 1.125rem;
    font-weight: 500;
    color: #333;
    text-align: center;
    word-break: keep-all;
}

.login_form {
    box-sizing: border-box;
    width: 600px;
    max-width: 100%;
    padding: 60px;
    margin: 30px auto 0;
    border: 1px solid #ddd;
    background-color: #f6f6f6;
}

.login_form input {
    width: 100%;
    height: 50px;
    padding: 0 20px;
}

.login_form input[type="text"] {
    margin: 0 0 10px 0;
}

.login_form button {
    margin: 30px 0;
    width: 100%;
    height: 50px;
    font-size: 1.125rem;
    background-color: #0766ee;
    color: #fff;
}

.login_form ul {
    display: block;
    text-align: center;
}

.login_form ul li {
    position: relative;
    display: inline-block;
    padding: 0 20px;
}

.login_form ul .list_after::after {
    content: '';
    display: block;
    position: absolute;
    top: 4px;
    left: 95px;
    width: 1px;
    height: 14px;
    background-color: #ddd;
    
}

.login_form ul .list_after2::after {
    content: '';
    display: block;
    position: absolute;
    top: 4px;
    left: 113px;
    width: 1px;
    height: 14px;
    background-color: #ddd;
    
}

.login_form ul li a {
    display: block;
    font-size: 0.875rem;
    color: #777;
    text-decoration: none;
}


